import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [{
    path: '/',
    name: 'IndexView',
    component: () => import('@/views/IndexView.vue'),
    meta: {
      title: "绝地求生--打开年末礼物，维寒迪雪地地图开放测试"
    }
  }]
})



